import React from 'react'
import PropTypes from 'prop-types'
import styled from 'styled-components'

const Container = styled.footer`
  position: fixed;
  left: 0;
  bottom: 0;
  height: 50px;
  width: 100%;
  color: #666;
  font-size: 14px;
  line-height: 50px;
  text-align: center;
`

const Footer = (props) => (
  <Container>
    {props.children}
  </Container>
)

Footer.propTypes = {
  /**
   * Color of footer's content
   */
  color: PropTypes.string
}

export default Footer